<template>
	<div class="inter_page1">
		<div class="cont">
			<el-row :gutter="20">
			  <el-col :span="showFaguiXq?12:24">
			  	<div class="grid-content">
			  		<div style="margin: 15px auto;" :style="{width:(showFaguiXq?'80%':'40%')}">
					  <el-input placeholder="请输入内容" v-model="input" class="input-with-select">
					    <el-button slot="append" icon="el-icon-search" @click="getFaguiList"></el-button>
					  </el-input>
					</div>
					<div style="max-height: 600px;min-height:200px;overflow-y: auto;">
						<div v-if="!faguiList.length">
							<img style="display:block;margin: 50px auto;" src="../../../assets/no-sj.png">
						</div>
						
		            	<div v-else-if="faguiList=='loading'" style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
							<i class="am-icon-spinner am-icon-spin"></i> loading
						</div>
						<div v-else>
							<ul class="faguiList" v-show="!showFaguiXq">
				                <li v-for="listItem in faguiList" class="item">
				                    <p class="item-head">
				                    	<span class="icon-fl">法规段落</span> 
				                    	<span @click="selectFaguiItem(listItem)" class="title" v-html="listItem.text">
					                    </span>
				                    </p>
				                    <p class="item-b">
				                    	<span>出自：</span>
						                《 {{listItem.biaoti}} 》
						            </p>
				                </li>
			            	</ul>
			            	<div class="faguiXq" v-show="showFaguiXq">
			            		<span  @click="showFaguiXq=!showFaguiXq" style="font-size: 18px;color: #b80000;line-height: 40px;cursor: pointer;"><i class="el-icon-d-arrow-left"></i> 返回上一步</span>
			            		<h2 style="text-align: center;line-height: 44px;font-size: 22px;">{{faguiXq.biaoti}}</h2>
			            		<p style="font-size: 18px;line-height: 24px;text-indent: 40px" v-html="faguiXq.text">
			            		</p>
			            	</div>
						</div>
					</div>
			  	</div>
			  	<div class="grid-content modal-content" v-if="showFaguiXq">
                	<div class="btn-add fr" @click="yd1=!yd1">+ 添加指标</div>
                    <div class="clear"></div>
                    <div class="text-area" :class="{hidden:yd1}">
                        <textarea v-model="ydText1" name="txt" class="area" placeholder="添加指标" rows="5"></textarea>
                        <p style="height: 50px;">
                            <button class="fr add" type="submit" @click="addQues1">添加</button>
                        </p>
                    </div>
                    <ul class="list" v-if="getQuestionData.length>0" style="max-height: 400px;overflow-y: auto;">
                        <li v-for="(item,index) in getQuestionData">
                            <div class="hidden" id="yd-id">{{item[0]}}</div>
                            <h3>指标{{index+1}}：<span class="yd">{{item[1]}}</span></h3>
                            <div class="fr sub" @click="appraise1(index)">提交</div>
                            <div class="am-form-group sel" style="margin-top:10px;">
                            	<el-radio-group v-model="radio1[index]">
                            	 	<el-radio :label="'0-'+item[0]">合理</el-radio>
										<el-radio :label="'1-'+item[0]">不合理</el-radio>
                            	</el-radio-group>
                            </div>
                        </li>
                    </ul>
                </div>
			  </el-col>
			  <el-col :span="12" v-if="showFaguiXq">
			  	<div class="grid-content" >
			  		<el-tabs v-model="activeName" @tab-click="handleClick">
					    <el-tab-pane label="指标积分排名" name="first">
					    	<div class="box" v-if="echartsBarData">
								<div v-if="!echartsBarData.value">
									<img style="display:block;margin: 0 auto;" src="../../../assets/no-sj.png">
								</div>
								<div v-else class="echartsBox">
									<Echarts id="EchartsBar" className="" options="optionBar" width="900px" height="650px" :data="echartsBarData"/>
								</div>
							</div>
					    </el-tab-pane>
					    <el-tab-pane label="指标关系图谱" name="second">
					    	<div class="box" v-if="echartsGraphData">
								<div v-if="!echartsGraphData.node">
									<img style="display:block;margin: 0 auto;" src="../../../assets/no-sj.png">
								</div>
								<div class="echartsBox" v-else>
									<Echarts id="relEcharts" className="" options="optionGraph" width="900px" height="650px" :data="echartsGraphData"/>
								</div>
							</div>
					    </el-tab-pane>
					    <el-tab-pane label="用户积分排名" name="third">
					    	<div class="box" v-if="echartsBarData2">
								<div v-if="!echartsBarData2.value">
									<img style="display:block;margin: 0 auto;" src="../../../assets/no-sj.png">
								</div>
								<div v-else class="echartsBox">
									<Echarts id="EchartsBar2" className="" options="optionBar" width="900px" height="650px" :data="echartsBarData2"/>
								</div>
							</div>
					    </el-tab-pane>
					    <el-tab-pane label="用户关系图谱" name="fourth">
					    	<div class="box" v-if="echartsGraphData2">
								<div v-if="!echartsGraphData2.node">
									<img style="display:block;margin: 0 auto;" src="../../../assets/no-sj.png">
								</div>
								<div v-else class="echartsBox">
									<Echarts id="relEcharts2" className="" options="optionGraph" width=" 900px" height="700px" :data="echartsGraphData2"/>
								</div>
							</div>
					    </el-tab-pane>
					</el-tabs>
			  	</div>
			  </el-col>
			</el-row>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Echarts from '../Echarts'
    import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	export default{
		name:'inter_page1',
		data(){
			return{
				showFaguiXq:false,
				activeName: 'first',
				echartsBarData:{},
				echartsGraphData:{},
				echartsBarData2:{},
				echartsGraphData2:{},
				input: '',
			    faguiList:[],
			    faguiXq:{},
			    yd1:true,
			    radio1:[],
			    ydText1:'',
			    id:'',
			    getQuestionData:[]
			}
		},
		components:{
			Echarts
		},
		computed:{
			userName(){
				if(sessionStorage.getItem('username')){
					return sessionStorage.getItem('username');
				}
			},
		},
		methods: {
			handleClick(tab, event) {
		        console.log(tab, event);
		    },
			selectFaguiItem(data){
				if(this.userName){
					let text="<span style='color:#b80000'>"+this.input+"</span>";
					data.text=data.text.replace(new RegExp(this.input, 'g'),text);
					this.faguiXq=data;
					this.id=data.md5_id;
					this.showFaguiXq=true;
					this.getJifenCihui();
		    		this.getQuestion1();
					this.getJifenUser();
					
				}else{
					alert("你还没登录哦！");
				}
			},
			getJifenUser(){
				this.echartsBarData2=false;
				this.echartsGraphData2=false;
				reqData
				    .req({
				      apiName: "barJifenUser",
				      data: {  
				      	  leibie:"cation"           
					      // qiye_id: this.company.id,
					      // yidian_name:this.getWord
				    }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				        data.bs="用户";
				       console.log(data);
				       this.echartsBarData2=data;
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			    reqData
				    .req({
				      apiName: "GraphJifenUser",
				      data: {    
				      	  leibie:"cation" 
				       //    qiye_id: this.company.id,
					      // yidian_name:this.getWord         
				       }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				        data.bs="用户";
				       console.log(data);
				       this.echartsGraphData2=data;
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			},
			getJifenCihui(){
				this.echartsBarData=false;
				this.echartsGraphData=false;
				reqData
				    .req({
				      apiName: "barJifenCihui",
				      data: {             
					      "qiye_id":"faguipingtai",
	                      "feilei_id":"faguipingtai",
	                      "yidian_name":this.id
				    }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				        data.bs="指标";
				       console.log(data);
				       this.echartsBarData=data;
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			    reqData
				    .req({
				      apiName: "GraphJifenCihui2",
				      data: {    
				          "qiye_id":"faguipingtai",
	                      "feilei_id":"faguipingtai",
	                      "yidian_name":this.id      
				       }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				        data.bs="指标";
				       console.log(data);
				       this.echartsGraphData=data;
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			},
			getFaguiList(){
				this.showFaguiXq=false;
				this.faguiList="loading";
				reqData
				    .req({
				      apiName: "shenji_yonghujiaohu_fagui_duanluochaxun",
				      data: {             
					      "name":this.input,
				    }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				       console.log(data);
				       this.faguiList=data;
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			},
			getQuestion1(){
				reqData
			        .req({
			          apiName: "showQues",
			          data: {   
			            qiye_id: "faguipingtai",          
			            yidian_name: this.id,
			            feilei_id:"faguipingtai"
			          }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            this.getQuestionData=data;    
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			},
			appraise1(index){
				if(this.radio1[index]){
					let n=this.radio1[index].split("-")[0];
					let id=this.radio1[index].split("-")[1];
					console.log(n+"====="+id);
					 reqData
				        .req({
				          apiName: "appraiseQues",
				          data: {             
				            // qiye_id: this.getWord,
				            // feilei_id:'yd-'+this.id,
				            yidian_id:id,
				            num:n,
				            username:this.userName
				          }
				        })
				        .then(res => {
				          res = res.data;
				          if (res && res.code === "200") {
				            let data = res.data;
				            alert("评价成功");
				            this.radio1=[];
				          }
				        })
				        .catch(err => {
				          console.log(err);
				       });
				}else{
					alert('请选择合理或不合理')
				}
			},
			addQues1(){
				console.log(this.id);
				reqData
				    .req({
				      apiName: "addQues",
				      data: {             
				      qiye_id: "faguipingtai",
				      yidian_name: this.id,
				      feilei_id:"faguipingtai",
				      username:this.userName,
				      yidian:this.ydText1
				    }
				    })
				    .then(res => {
				      res = res.data;
				      if (res && res.code === "200") {
				       let data = res.data;
				       alert("添加成功");
				       this.ydText1='';
				       this.yd1=true;
				       this.getQuestion1();
				       this.getJifenCihui();
				       this.getJifenUser();
				    }
				    })
				    .catch(err => {
				       console.log(err);
				    });	
			},

	    },
	    mounted(){
	    	
	    }

	}
</script>
<style type="text/css" scoped="">
	.inter_page1 .cont{
		width: 90%;
	    margin: 30px auto;
	    min-height: 600px;
	    /*padding: 40px;*/
	    box-sizing: border-box;
	} 
	.inter_page1 .cont .faguiList .item{
	    padding: 20px 0;
	    border-bottom: 1px solid #e0e0e0;
	}
	.inter_page1 .cont .faguiList .item p{
	    height: 28px;
	    line-height: 28px;
	    color: #999999;
	}
	.inter_page1 .cont .faguiList .item .item-head{
	    margin-bottom: 14px;
	    cursor: pointer;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	}
	.inter_page1 .cont .faguiList .item .item-head .icon-fl{
	    padding: 7px 12px;
	    background-color: #f59536;
	    border-radius: 5px;
	    color: #fff;
	}
	.inter_page1 .cont .faguiList .item .item-head .title{
	    color: #333;
	    font-size: 18px;
	    /*font-weight: bold;*/
	}
	.inter_page1 .cont .faguiList .item .item-head .title:hover{
		color: #b80000;
	}
	.inter_page1 .cont .faguiList .item .item-b .key{
	    color: #128bed;
	}
	.inter_page1 .grid-content{
		padding: 20px;
	    -webkit-box-sizing: border-box;
	    box-sizing: border-box;
	    border: 1px solid #ccc;
	    border-radius: 6px;
	    margin-bottom: 20px;
	}


	.inter_page1 .modal-content{
        /*padding-left: 16px;*/
        box-sizing: border-box;
    }
    .inter_page1 .am-form-group {
        margin-bottom: 0;
    }
   .inter_page1 .modal-content .list li{
        padding-top: 13px;
        padding-bottom: 13px;
        border-bottom: 1px solid #ebebeb;
    }
    .inter_page1 .modal-content .list li .sub{
        color: #b80000;
        cursor: pointer;
        margin-right: 10px;
    }
    .inter_page1 .modal-content .btn-add{
        color: #b80000;
        cursor: pointer;
        height: 58px;
        line-height: 58px;
    }
    .inter_page1 .modal-content  .text-area{
        width: 100%;
        margin: 0 auto;
    }
    .inter_page1 .modal-content  .text-area .area{
        width: 100%;
        border: 1px solid #ebebeb;
        color: #333;
        padding: 10px;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .inter_page1 .modal-content  .text-area button{
        outline: none;
        background-color: #b80000;
        border:none;
        padding: 6px 14px;
        margin-top: 10px;
        color: #fff;
        border-radius: 6px;
        cursor: pointer;
    }
    .inter_page1 .echartsBox{
    	overflow-x: scroll;
    }
	 /*.bg-purple {
    }
	}
    background: #d3dce6;
  }*/
 /* .bg-purple-light {
    background: #e5e9f2;
  }*/
</style>


